//
// LAYOUT / FLEX
//-----------------------------------------------------------------------

.l-flex {
  display: flex;
  align-items: center;
}

.l-flex--wrap {
  flex-wrap: wrap;
}

.l-flex--grow {
  flex-grow: 1;
}

.l-flex--apart {
  justify-content: space-between;
}

.l-flex-justify-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}